import React from 'react';
import logo from '../images/logo/logowhite.svg'
import { Link } from 'react-router-dom';
const NavBar = () => {
  return ( 
  <> 
  <nav className="navbar navbar-expand-lg navbar-dark">
  <Link className="navbar-brand" to="/"><img src={logo} width='48px'/></Link>
    <button
      className="navbar-toggler"
      type="button"
      data-toggle="collapse"
      data-target="#navbarTogglerDemo01"
      aria-controls="navbarTogglerDemo01"
      aria-expanded="false"
      aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
      
      <ul className="navbar-nav ml-auto mt-2 mt-lg-0">
        <li className="nav-item active">
          <a className="nav-link" href="https://www.facebook.com/clariceoyales" target='_blank'><i class="fab fa-facebook-square"></i>
          </a>
        </li>
        <li className="nav-item active">
          <a className="nav-link" href="https://twitter.com/clariceoyales" target='_blank'><i class="fab fa-twitter"></i>
          </a>
        </li>
        <li className="nav-item active">
          <a className="nav-link" href="https://www.instagram.com/clariceoyales/" target='_blank'><i class="fab fa-instagram"></i>
          </a>
        </li>
        <li className="nav-item active">
          <a className="nav-link" href="https://www.behance.net/clariceoyales" target='_blank'><i class="fab fa-behance"></i>
          </a>
        </li>
        <li className="nav-item active">
          <a className="nav-link" href="https://www.linkedin.com/in/clarice-joyce-oyales-0494b21a4/" target='_blank'><i class="fab fa-linkedin"></i>
          </a>
        </li>
        
      </ul>

    </div>
  </nav> 
  </>
  );
}

export default NavBar;