import React from 'react';
import svg from '../images/Asset 3.svg';
import image from '../images/pic.jpg'
const FooterContent = () => {
  return (
    <>
    <div className='footer__papertear'><img src={svg}/></div>
    <div className='footer__container'>
    <div className='footer__content'>
    <div className='footer__email'>
        <a href='mailto:cjloyales@gmail.com' target='_blank' className="footer-link">cjloyales@gmail.com</a>
        <ul className='footer__list'>
          <li className='footer__items'><a className="footer-link" href="https://www.facebook.com/clariceoyales" target='_blank'><i class="fab fa-facebook-square"></i></a></li>
          <li className='footer__items'><a className="footer-link" href="https://twitter.com/clariceoyales" target='_blank'><i class="fab fa-twitter"></i></a></li>
          <li className='footer__items'><a className="footer-link" href="https://www.instagram.com/clariceoyales/" target='_blank'><i class="fab fa-instagram"></i></a></li>
          <li className='footer__items'><a className="footer-link" href="https://www.behance.net/clariceoyales" target='_blank'><i class="fab fa-behance"></i></a></li>
          <li className='footer__items'><a className="footer-link" href="https://www.linkedin.com/in/clarice-joyce-oyales-0494b21a4/" target='_blank'><i class="fab fa-linkedin"></i></a></li>
        </ul>
      </div>
      <div className='footer__link'>
        <p>Visit my creative lounge:</p>
        <a href='https://cjloyales.wordpress.com' target='_blank' className="footer-link">https://cjloyales.wordpress.com</a>
      </div>
      <div className='footer__paragraph'>
        <p>If you can’t find me here, I’m out there—busy living and being.</p>
      </div>
    </div>
    <div className='footer__image'>
      <img src={image} width='200px' height='200px'/>
    </div>
    </div>
    
    
    </>
  );
}

export default FooterContent;
