import React from 'react';
import {socialMediaContents} from '../SocialMediaContentExport';
import NavBar from '../components/NavBar';
import image from '../images/Asset 3.svg'
const Social = () => {
  window.scrollTo(0,0);
  return (
    <> 
    <header className="header--page">
      <NavBar/>
    </header> 
      <main className='social'>
        <section className='section__header'>
          <h1>Social Media Content</h1>
          <img src={image}/>
        </section>
        <section className='section__content'>
          {
            socialMediaContents.map(socialMediaContent=>(
              <div class="card" key={socialMediaContent.id}>
                  <a href="#" class="card-body"><img class="card-img-top" src={socialMediaContent.img} alt={socialMediaContent.id}/></a>
              </div>
            ))
          }
        </section>
      </main>
    
    
    </>
  );
}

export default Social;
