import React from 'react';
import blog from '../images/Group 3.svg';
import video from '../images/Group 8.svg';
import social from '../images/Group 9.svg';
import {Link} from 'react-router-dom'
const Works = () => {
  return (
    <>
      <section id='section__works' className='section__works'>
      
      <div className='section__content works'>
        <h1>My Works</h1>
        <div className='works__content'>
        
        <div className='works__social'>
        <Link to='/socialmediacontent'>
        <img src={social} width='300px' height='200px'/>
          <h4>Social Media</h4>
          </Link>
        </div>
        <div className='works__blogs'>
        <Link to='/blogs'>
          <img src={blog} width='300px' height='200px'/>
          <h4>Blogs</h4>
          </Link>
        </div>
        <div className='works__videos'>
        <Link to='/videos'>
        <img src={video} width='300px' height='200px'/>
          <h4>Videos</h4>
          </Link>
        </div>
        </div>
        </div>
      </section>
    </>
  );
}

export default Works;
