import React from 'react';
import NavBar from '../components/NavBar';
import image from '../images/Asset 3.svg';
import {blogs} from '../BlogExport';
const Blogs = () => {
  window.scrollTo(0,0);
  return (
    <> 
    <header className="header--page">
      <NavBar/>
    </header> 
      <main className='blogs'>
        <section className='section__header'>
          <h1>Blogs</h1>
          <img src={image}/>
        </section>
        <section className='section__content'>
          {
            blogs.map(blog=>(
              <div class="card" key={blog.id}>
                  <a href={blog.link} target='_blank' class="card-body"><img class="card-img-top" src={blog.img} alt={blog.id}/></a>
              </div>
            ))
          }
        </section>
      </main>
    
    
    </>
  );
}

export default Blogs;
