import React from 'react';
import svg from '../images/Group 6.svg'
const Career = () => {
  return (
    <>
      <section id='section__career' className='section__career'>
      <div className='section__content section__content--flex career'>
      <div className='career__svg'>
        <img src={svg}/>
      </div>
      <div className='career__content'>
      <p>A creative writer who dig deeps into self and generates more, newer, and better ideas to improve essential parts of life. A performance-driven artist who keeps the production pipeline in good working order through dealing with tasks head-on. A gritty professional with strong educational background and experiences in the communication industry as a writer and editor. Equipped with exceptional writing proficiency, coupled with impeccable verbal, written, and presentation skills. Glutted with a positive attitude, boundless energy, and a growing curiosity to learn and acquire new knowledge and skills. Known for being organized and capable to multitask in a competitive, challenging, and fast-paced environment.  </p>
      </div>
      </div>
      </section>
    </>
  );
}

export default Career;
