import React from 'react';
import Home from './pages/Home'
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  
} from "react-router-dom";
import Blogs from './pages/Blogs';
import Social from './pages/Social';
import Videos from './pages/Videos';
function App() {
  return (
<>
<Router>
        <Switch>
          <Route path="/blogs">
            <Blogs />
          </Route>
          <Route path="/videos">
            <Videos />
          </Route>
          <Route path="/socialmediacontent">
            <Social />
          </Route>
          <Route path="/">
            <Home />
          </Route>
          </Switch>
        </Router>
</>
    
  );
}

export default App;
