import React from 'react';
import image from '../images/2019-02-07.jpg'
const HeaderContent = () => {
  return (
    <>
      <div className='header__filter'></div>
      <div className="header__banner"></div>
    <div className="header__content">
      <div className="header__image"><img src={image} width='240px' height='240px' style={{borderRadius:'100%', border:'8px solid #eeeeee'}}/></div>
      <div className='header__text'>
        <h1 className="header__maintext">Clarice Joyce Oyales</h1>
        <h3 className="header__subtext">Digital Content Writer | Copy Editor</h3>
        <p className="header__paragraph">A Communication-Multimedia degree holder who has digital content writing in one hand and the rest of her creative pursuits in the other.</p>
      </div>
    </div>
    </>
  );
}

export default HeaderContent;
