import React from 'react';
import image from '../images/Asset 6.svg';
import svg from '../images/Group 7.svg';
const Experties = () => {
  return (
    <>
      <section id='section__experties' className='section__experties experties'>
      <div className='section__content section__content--flex'>
        <div className='experties__content'>
          <p>Social Media Content Writing | Digital Content and Creative Blogs Writing | Digital Marketing and Promotion | Product Description and Development | Web Content | Public Speaking | TV and Stage Production | Events Management | Community Engagement | Presentation Development | Communication and Research | Email Marketing | Brand Development | Project and Campaign Planning and Development |</p>
        </div>
        <div className='experties__svg'>
          <img src={svg}/>
        </div>
        </div>
        <div className='experties__papertear'><img src={image}/></div>
      </section>

    </>
  );
}

export default Experties;
