import React from 'react';
import {videoContents} from '../VideoExport';
import NavBar from '../components/NavBar';
import image from '../images/Asset 3.svg'
const Videos = () => {
  window.scrollTo(0,0);
  return (
    <> 
    <header className="header--page">
      <NavBar/>
    </header> 
      <main className='videos'>
        <section className='section__header'>
          <h1>Videos</h1>
          <img src={image}/>
        </section>
        <section className='section__content'>
          {
            videoContents.map(videoContent=>(
              <div class="card" key={videoContent.id}>
                  <a href={videoContent.link}><img class="card-body" src={videoContent.img} alt={videoContent.id}/></a>
              </div>
            ))
          }
        </section>
      </main>
    
    
    </>
  );
}

export default Videos;
